import * as service from '../services/purchase.service';

export default class User {

    constructor(user = {}){
        this.initUser(user);
    }

    initUser(userData = {}){
        
        if( User.hasMinUserDataRequired(userData) ){
            this.email = userData.email;
            this.id = userData.id;
            userData.token ? this.token = userData.token : '';
            User.saveUser(this.userStoreInfos);
        } else {
            console.log('User.setUserData missing required data to create an user', {userData});
            return false;
        }

    }

    static hasMinUserDataRequired(userData){
        if(!userData.email) return false;
        if(!userData.id) return false;
        return true;
    }

    get userStoreInfos(){
        return {
            email: this.email ? this.email : '',
            token: this.token ? this.token : '',
            id: this.id ? this.id : ''
        }
    }

    get userLogged(){
        if(this.token) 
            return true;
        return false;
    }

    static saveUser(user = {}){
        let userJSON = JSON.stringify(user);
        window.localStorage.setItem('user', userJSON);
        window.sessionStorage.setItem('user', userJSON);
    }

    static async login(credential = {email: '', password: ''}){

        if(credential.email && credential.password){
            let body = credential;
            return service.send("POST", body, 'loginUser').then(data=>{
                if(data.id){
                    return new User(data);
                }else {
                    return {error:data}
                }
            }).catch(error=>{
                console.log({error})
            })
        }

    }

    static async getUserByToken(token){
        let route = 'http://localhost:1337/user/token';
        let headers = new Headers();
        headers.append('authorization', "0Nk68KO894f " + token);
        if(route){
            return fetch(route, {
                headers: headers
            }).then(data=>{
                if(!data) return;
                data = data.json();
                return data;
            }).catch(error=>{
                console.warn({fetchError: error});
            })
        }
    }

    static async findOrSetStoredUser(){
        let userSessionStored = User.userSessionStored;
        if(userSessionStored && userSessionStored.email){
            return new User(userSessionStored);
        }else {
            let userLocalStored = User.userLocalStored;
            if(userLocalStored && userLocalStored.token){
                let userFound = await User.getUserByToken(userLocalStored.token);
                if(userFound.email){
                    User.saveUser(userFound);
                } else {
                    return;
                }

            }else{
                return;
            }
        }
    }

    static get userLocalStored(){
        let userStored = window.localStorage.getItem('user');
        if(userStored){
          let userParsed = JSON.parse(userStored);
          if(userParsed && userParsed.email){
            return userParsed;
          }
        }
        return;
    }

    static get userSessionStored(){
        let userStored = window.sessionStorage.getItem('user');
        if(userStored){
            let userParsed = JSON.parse(userStored);
            if(userParsed && userParsed.email){
                return userParsed;
            }
        }
    }
    

}