import Vue from 'vue'
import Router from 'vue-router'
import Purchase from './views/Purchases.vue'
import Login from './views/Login.vue'
import store from './store';

async function isUserConnected(to,from,next){
  let user = await store.getters.userConnected;
  if(!user || !user.email){
    next('/login')
  }else {
    next();
  }
}

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'Purchases',
      component: Purchase,
      beforeEnter: isUserConnected
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    }
  ]
})
