import Vue from 'vue'
import Vuex from 'vuex'

import User from './models/User.model';

Vue.use(Vuex)

const types = {
  SET_USER: 'SET_USER'
}

const localStore = window.localStorage;

export default new Vuex.Store({
  state: {
    user: {}
  },
  mutations: {

    setUser(state, user = {}){
      state.user = user;
      if(user.userStoreInfos){
        localStore.setItem('user', JSON.stringify(user.userStoreInfos));
      }
    }

  },
  actions: {

    

  },
  getters: {

     async userConnected(state) {

      if(state.user.email){
        return state.user;
      }

      let userStored = await User.findOrSetStoredUser();
      if(userStored){
        return userStored;
      }

      return {};
    }

  }

})
