<template>    
    <div class="white">

        <v-toolbar color="primary">
            <v-toolbar-title class="white--text">{{purchaseTitle}}</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark @click="exitPurchaseForm">
                <v-icon>close</v-icon>
            </v-btn>
        </v-toolbar>

        <v-container>

            <v-form
                v-model="newPurchaseForm"
                ref="newPurchaseForm"
            >

                <v-text-field
                    label="Montant"
                    single-line
                    :rules="amountRule"
                    v-model="newPurchase.amount"
                    v-on:keyup="checkAmount"
                    :readonly="isOnlyRead"
                ></v-text-field>

                <v-select
                    :items="accountTypes"
                    :item-text="accountType => accountType.name"
                    :item-value="accountType => accountType.id"
                    label="Account Type"
                    v-model="newPurchase.accountType"
                    :rules="notEmpty"
                    :readonly="isOnlyRead"
                    required
                ></v-select>

                <v-select
                    :items="purchaseTypes"
                    :item-text="purchaseType => purchaseType.name"
                    :item-value="purchaseType => purchaseType.id"
                    label="Purchase Type"
                    v-model="newPurchase.purchaseType"
                    :rules="notEmpty"
                    :readonly="isOnlyRead"
                    required
                ></v-select>

                <v-layout>

                    <v-flex mb-6>
                        
                        <v-menu
                            ref="menu"
                            v-model="menu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            :return-value.sync="date"
                            lazy
                            transition="scale-transition"
                            offset-y
                            full-width
                            min-width="290px"
                            :disabled="isOnlyRead"
                        >
                            <template v-slot:activator="{ on }">
                                <v-text-field
                                    v-model="date"
                                    label="Buying date"
                                    prepend-icon="event"
                                    v-on="on"
                                ></v-text-field>
                            </template>
                            <v-date-picker v-model="date" no-title scrollable>
                                <v-spacer></v-spacer>
                                <v-btn flat color="primary" @click="menu = false">Cancel</v-btn>
                                <v-btn flat color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                            </v-date-picker>
                        </v-menu>

                    </v-flex>

                    <v-flex mb-6
                        ><v-menu
                            ref="datetimeMenu"
                            v-model="datetimeMenu"
                            :close-on-content-click="false"
                            :nudge-right="40"
                            :return-value.sync="time"
                            lazy
                            transition="scale-transition"
                            offset-y
                            full-width
                            max-width="290px"
                            min-width="290px"
                            :disabled="isOnlyRead"
                        >
                            <template v-slot:activator="{ on }">
                            <v-text-field
                                v-model="time"
                                label="Picker in menu"
                                prepend-icon="access_time"
                                readonly
                                v-on="on"
                            ></v-text-field>
                            </template>
                            <v-time-picker
                            v-if="datetimeMenu"
                            v-model="time"
                            full-width
                            @click:minute="$refs.datetimeMenu.save(time)"
                            ></v-time-picker>
                        </v-menu>
                    </v-flex>

                </v-layout>

                
                <v-btn v-if="!isOnlyRead" block @click="newPurchaseFormValid" align-center justify-space-between row color="primary">
                    
                    <v-flex>Envoyer</v-flex> 
                    <v-icon>add</v-icon>
                
                </v-btn>

            </v-form>

            <v-layout>
                <v-flex mb-6>
                    <v-btn v-if="isOnlyRead" @click="openConfirmRemovePurchaseDialog" block flat color="warning">Supprimer</v-btn>
                </v-flex>
                <v-flex mb-6>
                    <v-btn v-if="isOnlyRead" @click="isOnlyRead = false" block flat color="primary">Modifier</v-btn>
                </v-flex>
            </v-layout>


            <v-dialog
                v-model="confirmRemoveDialog"
                width="500"
            >
                <v-card>
                    <v-card-title
                        class="headline grey lighten-2"
                        primary-title
                    >
                        Supprimer cette achat?
                    </v-card-title>
                    <v-card-actions>
                        <v-layout>
                            <v-flex mb-6>
                                <v-btn
                                    block
                                    flat
                                    color="primary"
                                    @click="confirmRemoveDialog = false"
                                >Annuler</v-btn>
                            </v-flex>
                            <v-flex mb-6>
                                <v-btn
                                    block
                                    flat
                                    color="warning"
                                    @click="confirmRemove"
                                >Confirmer</v-btn>
                            </v-flex>
                        </v-layout>
                    </v-card-actions>
                </v-card>
            </v-dialog>

            

        </v-container>

    </div>
</template>

<script>    

import * as purchaseService from '../services/purchase.service';

export default {
    props: ['purchaseToForm','readOnly', 'purchaseTitle'],
    data: function(){
        return {
            accountTypes: [],
            purchaseTypes: ['purchaseTypes1','purchaseTypes2','purchaseTypes3'],
            menu:false,
            date: new Date().toISOString().substr(0, 10),
            time: `${new Date().getHours()}:${new Date().getMinutes()}`,
            datetimeMenu: false,
            amountRule: [
                (v) =>  /^\d+(,\d+)*$/.test(v) || 'Format: 00(,00)'
            ],
            notEmpty: [
                (v) => !!v  || 'Need to select '
            ],
            newPurchase: {
                accountType: '',
                purchaseType: '',
                timestamp: '',
                amount: ''
            },
            newPurchaseForm: '',
            isOnlyRead: false,
            confirmRemoveDialog: false
        }
    },
    created: function() {
        purchaseService.get('getAccountTypes').then(accountTypes=>{
            this.accountTypes = accountTypes          
        })
        purchaseService.get('getPurchaseTypes').then(purchaseTypes=>{
            this.purchaseTypes = purchaseTypes
        })
    },
    watch: {
        purchaseToForm: function(val,oldVal){
            this.newPurchase = val;
            this.isOnlyRead = this.readOnly
            this.setFormTimeFromTimestamp(this.newPurchase.timestamp)
            this.$refs.newPurchaseForm.resetValidation()
        }
    },
    methods: {
        newPuchaseDateTime: function(){
            return new Date(this.date+" "+this.time).getTime()
        },
        newPurchaseFormValid(){
            this.newPurchase.timestamp = this.newPuchaseDateTime();
            this.checkAmount();
            if(this.$refs.newPurchaseForm.validate()){                
                this.$emit('purchaseFormSubmit', this.newPurchase);
            }else{
                console.log(this.$refs.newPurchaseForm);
            }
        },
        exitPurchaseForm(){  
            this.$emit('exitPurchaseForm', this.newPurchase);
        },
        checkAmount(){
            let decimal = false;
            let countDecimal = 0
            this.newPurchase.amount = this.newPurchase.amount.split('').filter(val => {
                if( !Number.isInteger(parseInt(val)) && val !== ',' ) {
                    return false;
                }
                if( val === ',' && decimal){
                    return false;
                }
                if( val === ',' && !decimal){
                    decimal = true;
                }
                return true;
            }).join('');
        },
        setFormTimeFromTimestamp(timestamp = 0){
            let date = new Date(timestamp);
            this.date = date.toISOString().substr(0, 10);
            this.time = `${date.getHours()}:${date.getMinutes()}`;
        },
        openConfirmRemovePurchaseDialog(){
            this.confirmRemoveDialog = true;
        },
        confirmRemove(){
            this.$emit('exitAndRemovePurchase', this.newPurchase );
        }
    }
}
</script>