<template>
    <v-container>

        <h1>$)</h1>

        <v-form
            v-model="userForm"
            ref="userForm">

            <v-flex>
                <v-text-field
                    label="Login"
                    :rules="notEmpty"
                    v-model="email"
                ></v-text-field>
            </v-flex>
            <v-flex>
                <v-text-field
                    v-model="password"
                    :append-icon="showPassword ? 'visibility' : 'visibility_off'"
                    :type="showPassword ? 'text' : 'password'"
                    label="Password"
                    :rules="notEmpty"
                    @click:append="showPassword = !showPassword"
                ></v-text-field>
            </v-flex>

            <p v-if="errorMessage.length > 0">{{errorMessage}}</p>

            <v-btn block @click="tryToLogin">Se connecter</v-btn>

            <v-btn @click="showUser" block>showBtn</v-btn>
            <router-link to="/">Go to Bar</router-link>
        </v-form>
        
    </v-container>
</template>



<script>
import User from '../models/User.model';
import store from '../store';
import router from '../router';

export default {
    data: function(){
        return {
            showPassword: false,
            email: '',
            password: '',
            notEmpty: [
                (v) => !!v  || 'Required'
            ],
            userForm: '',
            errorMessage: ''
        }
    },
    methods: {
        async showUser(){
            let userWaited = await store.getters.userConnected;
            console.log({ux: userWaited })
        },
        async tryToLogin(){
            if(this.$refs.userForm.validate()){

                let credentials = {
                    email: this.email,
                    password: this.password
                }
                
                await User.login(credentials).then(data=>{
                    if(data instanceof User){

                        store.commit('setUser', data);
                        router.push('/')

                    }else if(data.error && typeof data.error === 'string'){
                        this.errorMessage = data.error;
                    }else{
                        console.error({userLoginError: data})
                    }
                })
            }
        }
    }
}
</script>

<style>
    h1 {
        text-align: center;
        font-size: 12em;
        transform: rotate(90deg);
    }
</style>
