import store from '../store';
import User from '../models/User.model';

async function getAuthorization(){
    let user = await store.getters.userConnected;
    if(user instanceof User){
       return `0Nk68KO894f ${user.token}`; 
    }
    return "";
};

export const baseDir = "http://localhost:1337/"

export const routes = {

    getPurchases: baseDir + "purchase",
    postPurchase: baseDir + "purchase",
    removePurchase: baseDir + "purchase",
    getAccountTypes: baseDir + "accountType",
    getPurchaseTypes: baseDir + "purchaseType",

    loginUser: baseDir + "user/login"

}

export async function get(routeName = ""){
    let route = routes[routeName];
    let headers = new Headers();
    await headers.append( 'authorization', await getAuthorization() );
    if(route){
        return fetch(route, {
            headers
        }).then(data=>{
            if(!data) return;
            data = data.json();
            return data;
        }).catch(error => {

            console.warn({fetchError: error});
        })
    }
}

export async function send(method="", body={}, routeName="", elementId = 0){

    let route = routes[routeName];
    let headers = new Headers();
    await headers.append('authorization', await getAuthorization() );

    if(elementId!=0){
        route += '/'+elementId
    }

    if(route){
        return fetch(route, {
            method: method,
            body: JSON.stringify(body),
            headers
        }).then(data=>{
            if(!data) return;
            data = data.json();
            return data;
        }).catch(error=>{
            console.warn({fetchError: error});
        })
    }
}