<template>

    <div>
        <v-toolbar color="primary">
            <v-toolbar-title class="white--text">PURCHASES</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-btn icon dark @click="openNewPurchaseForm">
                <v-icon>add</v-icon>
            </v-btn>
        </v-toolbar>

        <v-container>

            <v-data-table
                :headers="headers"
                :items="purchases"
                hide-actions
                @select="selectPurchase"
            >
                <template v-slot:items="props">
                    <tr @click="selectPurchase(props.item)">
                        <td>{{ props.item.amount }}</td>
                        <td>{{ props.item.purchaseType.name }}</td>
                        <td>{{ props.item.accountType.name }}</td>
                        <td class="text-xs-right">{{ props.item.dateStr }}</td>
                    </tr>
                </template>
                
            </v-data-table>

            <v-dialog v-model="showPurchaseForm" persistent>
                <PurchaseForm 
                    @purchaseFormSubmit="sendPurchase" 
                    @exitPurchaseForm="showPurchaseForm = false"
                    @exitAndRemovePurchase="removePurchase"
                    v-bind:purchaseTitle="purchaseTitle"
                    v-bind:readOnly="purchaseFormReadOnly" 
                    v-bind:purchaseToForm="purchaseToForm"
                />
            </v-dialog>

        </v-container>

        <v-snackbar
            v-model="alertNewPurchaseSent"
            :color="purchaseSubmissionType"
            @click="alertNewPurchaseSent = false"
            >
                {{purchaseSubmissionMessage}}
            <v-btn flat >Close</v-btn>
        </v-snackbar>

    </div>
</template>

<script>

import PurchaseForm from '../components/PurchaseForm';

import * as purchaseService from '../services/purchase.service';
import PurchaseModel from '../models/Purhcase.model';

export default {
    components: {
        PurchaseForm,
    },
    data () {
        return {
            headers: [
                {
                    text: 'Amount',
                    value: 'amount',
                    align: 'left'
                },
                {
                    text: 'Type',
                    value: 'purchaseType.name',
                    align: 'left'
                },
                {
                    text: 'Account',
                    value: 'accountType.name',
                    align: 'left'
                },
                {
                    text: 'Date',
                    value: 'timestamp',
                    align: 'right'
                }
            ],
            expand: false,
            purchases: [],
            showPurchaseForm: false,
            alertNewPurchaseSent: false,
            showPurchaseComponent: false,
            purchaseFormReadOnly: false,
            purchaseToForm: {},
            purchaseTitle: "New purchase",
            purchaseSubmissionMessage: 'New purchase has been sent',
            purchaseSubmissionType: 'success'
        }
    },
    created () {

        this.fetchPurchases();

    },
    methods: {
        fetchPurchases (){
            purchaseService.get('getPurchases').then(purchases => {
                if(Array.isArray(purchases)){
                    let purchasesModeled = this.getPurchaseModels(purchases);
                    this.purchases = [];
                    this.purchases = purchasesModeled;
                    this.purchasesToDisplay = purchasesModeled ;
                }else{
                    console.warn('no purchases found or error happend');
                }
            })
        },
        selectPurchase (purchaseSelected) {
            this.purchaseTitle = "Purchase"
            this.purchaseFormReadOnly = true;
            this.purchaseToForm = purchaseSelected;
            this.showPurchaseForm = true;
        },
        sendPurchase(newPurchase){
            let method = Number.isInteger(newPurchase.id) && newPurchase.id > 0 ? "PATCH" : "POST";
            let patchId = newPurchase.id ? newPurchase.id : 0;
            if(newPurchase.formData) newPurchase = newPurchase.formData;       
            purchaseService.send(method, newPurchase, 'postPurchase', patchId).then(purchaseCreated=>{
                if(purchaseCreated && purchaseCreated.amount){
                    this.showPurchaseForm = false;
                    this.showSnackbar('New purchase has been sent');
                    this.fetchPurchases();
                }
                else {
                    this.showPurchaseForm = false;
                    this.showSnackbar('Error happened while sending purchase', 'error');
                }
            })
        },
        getPurchaseModels (purchases){
            let purchasesModeled = [];
            for(let purchase of purchases){
                let pm = new PurchaseModel(purchase);
                purchasesModeled.push(pm);
            }
            return purchasesModeled;
        },
        openNewPurchaseForm(){
            this.showPurchaseForm = true;
            this.purchaseToForm = new PurchaseModel();
            this.purchaseFormReadOnly = false;
            this.purchaseTitle = "New purchase";
        },
        removePurchase(purchase = {}){
            this.showPurchaseForm = false;
            if(purchase.id){
                purchaseService.send("delete",'', "removePurchase", purchase.id).then(purchaseRemoveResult=>{
                    if(purchaseRemoveResult.id){
                        this.showSnackbar('Purchase deleted');
                        this.fetchPurchases();
                    }else{
                        this.showSnackbar('Purchase delete error', 'error');
                    }
                })
            }
        },
        showSnackbar(message = "", type = 'success'){
            this.purchaseSubmissionMessage = message;
            this.purchaseSubmissionType = type;   
            this.alertNewPurchaseSent = true;         
        }
    },
}
</script>
