export default class PurchaseModel {

  constructor(purchase = {}){
    this.amount = purchase.amount ? purchase.amount : '';
    this.note = purchase.note ? purchase.note : '';
    this.accountType = purchase.accountType ? purchase.accountType : '';
    this.purchaseType = purchase.purchaseType ? purchase.purchaseType : '';
    this.timestamp = purchase.timestamp ? purchase.timestamp : new Date().getTime();
    this.id = purchase.id ? purchase.id : '';
  }

  get dateStr(){
    let date = new Date(this.timestamp);
    let dateStr = date.toLocaleDateString();
    return dateStr;
  }

  get hourStr() {
    let date = new Date(this.timestamp);
    return `${date.getHours()}:${date.getMinutes()}`;  
  }

  get dateHourStr(){
    return `${this.dateStr} - ${this.hourStr}`;
  }

  get formData(){
    return {
      amount: this.amount,
      accountType: Number.isInteger(this.accountType) ? this.accountType : this.accountType.id,
      purchaseType: Number.isInteger(this.purchaseType) ? this.purchaseType : this.purchaseType.id,
      timestamp: this.timestamp,
      note: this.note
    }
  }



}

